'use client';
import CardComponent from "./components/CardComponent";
import { getAllJobPostings } from "./lib/jobs";
import { Posting } from "./types/job";
import { useEffect, useState } from "react";

export default function Home() {
  const [data, setData] = useState<Posting[]>([]);
  const [sortOption, setSortOption] = useState("newest");

  useEffect(() => {
    const fetchData = async () => {
      const jobs = await getAllJobPostings();
      setData(sortJobs(jobs, sortOption));
    };
    fetchData();
  }, [sortOption]);

  const sortJobs = (jobs: Posting[], option: string) => {
    return [...jobs].sort((a, b) => {
      if (option === "newest") {
        return new Date(b.about.posted_on).getTime() - new Date(a.about.posted_on).getTime();
      } else if (option === "oldest") {
        return new Date(a.about.posted_on).getTime() - new Date(b.about.posted_on).getTime();
      } else if (option === "deadline") {
        return new Date(a.about.deadline).getTime() - new Date(b.about.deadline).getTime();
      }
      return 0;
    });
  };

  return (
    <div className="gap-[40px] pt-[72px] pb-[72px] pl-[124px] pr-[123px]">
      <div className="flex justify-between items-center mb-8">
        <div className="px-5">
          <p className="font-Poppins font-[900] text-[32px] leading-[1.2] text-[#25324B]">
            Opportunities
          </p>
          <p className="font-Epilogue font-[400] text-[16px] leading-[1.6] text-[#7C8493]">
            Showing {data.length} results
          </p>
        </div>
        <div className="flex items-center gap-4">
          <label className="text-[#25324B] font-medium">Sort by:</label>
          <select
            className="border border-[#ccc] rounded px-4 py-2 text-sm text-[#25324B]"
            value={sortOption}
            onChange={(e) => setSortOption(e.target.value)}
          >
            <option value="newest">Newest First</option>
            <option value="oldest">Oldest First</option>
            <option value="deadline">Nearest Deadline</option>
          </select>
        </div>
      </div>

      <div className="grid gap-6">
        {data.map((job: Posting, index: number) => (
          <CardComponent key={job.id} job={job} index={index} />
        ))}
      </div>
    </div>
  );
}
